		// }
	},

	computed: {
		thAlignCpd() {
			let nameAlign = '';
			switch (this.thAlign) {
				case 'left':
					nameAlign = 'flex-start'
					break;
				case 'center':
					nameAlign = 'center'
					break;
				case 'right':
					nameAlign = 'flex-end'
					break;
				default:
					nameAlign = 'center'
					break;
			}
			return nameAlign
		}
	}
};
